@import url(https://fonts.googleapis.com/css2?family=M+PLUS+2:wght@400;500&family=Roboto+Slab:wght@500&family=Roboto:wght@400;500&family=Ubuntu:ital,wght@0,400;0,500;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  font-family: 'Roboto', sans-serif;
}
.App{
  background-color: black;
  color: white;
}

body{
  margin-right: 0;
}
.row{
    padding-left: 1rem;
    
}

.row_poster{
    width: 100vw;
    max-height: 100px;
    object-fit: contain;
    transition: ease 450ms;
    cursor: pointer;
    margin-left: 20px;
}
.row_poster:hover{
    transform: scale(1.08)
}

.row_container{
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
}

.row_container::-webkit-scrollbar{
    display: none;
}

.row_poster_larger{
    max-height: 250px;
}
.row_poster_larger:hover{
    transform: scale(1.09);
}

.row_title{
    padding-top: 1rem;
    padding-bottom: 1rem;
    
}
.baner{
    height: 450px;
    object-fit: contain;
    padding-top: 2rem;
    color: white;
    
}
.baner_contents{
    margin-left: 1rem;
    
}
.baner_overview{
    max-width: 360px;
}
.baner_buttons{
    padding: 1rem 0;
}
.baner_button{
    padding: 0.5rem 1rem;
    cursor: pointer;
    background: none;
    border: none;
    border-radius: 8px;
    color: white;
    font-size: large;
    margin-right: 0.5rem;
    transition: ease-in-out 450ms;
}
.baner_button:hover{
    background-color: red;
    transform: scale3d(1.08);
}

.fade_buttons{
    height: 16rem;
    background-image: linear-gradient(
        transparent,
        black
    );

}
